// import Vue from "@/main";
import axios from "axios";
import store from "../store";
import {
  Message
} from "element-ui";
// import commonConfig from '../config.js';
const loadingTime = 500;
const timeOut = 200000;
var _loading_ = window.$httpLoading;
let httpRequest = axios.create()
// axios.defaults.timeout = 5000;
httpRequest.interceptors.request.use(
  config => {
    // console.log('!',config)
    if (store.state.token) {
      config.headers.token = store.state.token;
    }
    // config.transformRequest = [function (data) {
    //   // data
    //   console.log(data)
    //   data = qs.stringify(data)
    //   console.log(data)
    //   return data
    // }]
    return config;
  },
  function (error) {
    // console.log('!',error)
    return Promise.reject(error);
  }
);
//
httpRequest.interceptors.response.use(
  res => {
    //  200
    //  => 
    //  
    if (res.status == 200 && res.data && res.data.code == '0') {
      _loading_.close();
      return Promise.resolve(res);
    } else if (res.status == 200 && res.data.type) {
      _loading_.close();
      //   => 
      return Promise.resolve(res);
    } else {
      _loading_.close();
      //   => 
      return Promise.reject(res);
    }
  },
  error => {
    // Vue.$httpLoading.close();
    _loading_.close();
    console.log("!", error);
    console.log(error.response);
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.msg = "(400)";
          break;
        case 401:
          error.msg = "(401)";
          break;
        case 403:
          error.msg = "(403)";
          break;
        case 404:
          error.msg = "(404)";
          break;
        case 408:
          error.msg = "(408)";
          break;
        case 500:
          error.msg = "(500)";
          break;
        case 501:
          error.msg = "(501)";
          break;
        case 502:
          error.msg = "(502)";
          break;
        case 503:
          error.msg = "(503)";
          break;
        case 504:
          error.msg = "(504)";
          break;
        case 505:
          error.msg = "HTTP(505)";
          break;
        default:
          error.msg = `(${error.response.status})!`;
      }
    }
    else if (error.message.indexOf('timeout') !== -1) {
      error.msg = "!"
    }
    else {
      error.msg = "!";
    }
    Message.error(error.msg);
    return;
    // return Promise.reject(error)
  }
);

function checkLoading(config) {
  config = config || {}
  let loading = config.showLoading || config.showLoading === undefined ?
    true :
    config.showLoading
  return loading

}

//   
function resolveData(showLoading, config, callBack) {
  // params => showLoading: loading , LOADING_TIME =>   500
  // callback => 
  return new Promise((resolve) => {
    if (showLoading) {
      let time = config.loadingTime || loadingTime;
      _loading_.show();
      setTimeout(() => {
        resolve(config);
      }, time);
    } else {
      _loading_.close();
      resolve(config);
    }
  }).then(res => {
    // 
    return callBack(res)
  })
}

/**
 * config
 * : 
 * url => 
 * params => 
 * isLoading => loading
 * loadingTime => loading,  500 
 * timeout => ,  5000 
 */

// GET 
export function requestGet(config) {
  let showLoading = checkLoading(config) //  loading
  return resolveData(showLoading, config, (res) => {
    return httpRequest.get(res.url, {
      params: res.data
    }, {
      timeout: config.timeOut || timeOut
    });
  })
}


// GET 
export function urlGetRequest(config) {
  let showLoading = checkLoading(config) //  loading 
  let data = config.data;
  for (var i in data) {
    if (config.url.indexOf('?') === -1) {
      config.url += '?' + i + '=' + data[i]
    } else {
      config.url += '&' + i + '=' + data[i]
    }
  }
  return resolveData(showLoading, config, (res) => {
    return httpRequest.get(res.url, {
      timeout: config.timeOut || timeOut
    });
  })
}


// POST 
export function requestPost(config) {
  let showLoading = checkLoading(config) //  loading
  let formData = config.data;
  return resolveData(showLoading, config, (res) => {
    return httpRequest.post(res.url, formData, {
      timeout: config.timeOut || timeOut,
      headers: {
        'Content-Type': 'application/json;charset=UTF-8'
      },
    });
  })
}

// 
export function requestFile(config) {
  let showLoading = checkLoading(config) //  loading
  let formData = new FormData();
  Object.keys(config.data).forEach(key => {
    formData.append(key, config.data[key]);
  });
  return resolveData(showLoading, config, (res) => {
    return httpRequest.post(res.url, formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      timeout: config.timeOut || timeOut
    });
  })
}

// 
export function requestExcel(config) {
  let showLoading = checkLoading(config) //  loading
  let formData = new FormData();
  Object.keys(config.data).forEach(key => {
    formData.append(key, config.data[key]);
  });
  return resolveData(showLoading, config, (res) => {
    return httpRequest.post(res.url, formData, {
      headers: {
        'responseType': 'blob',
        'Content-Type': 'application/json;charset=UTF-8'
      },
      timeout: config.timeOut || timeOut
    });
  })
}